<style lang="scss" scoped>
.register{
    background-color: #304156;
    width: 100%;
    padding: 20px 40px 0 0;
    text-align: left;
    .title{
        color: white !important;
        text-align: center;
        margin-bottom: 50px;
    }
    .register_form{
        // padding: 200px 0 0 0;
        .form_item{
            color: white;
            .form_item_input{
                width: 250px; 
                
            }
            .item_radio{
                color: white;
            }
            .label_text{
                    color: white !important;
                }
        }
    }

}
</style>
<style scope>
/* .el-form-item__label{
    font-size: 16px;
    color: white ;
    font-weight: bold;
    padding-right: 40px
} */
</style>
<template>
    <div class="item_column register">
        <h1 class="title">
                用户注册
        </h1>
        <div class="item_row register_box">
            <el-form class="register_form" label-width="120px" :model="personInfo" :rules="formRule" ref="registerForm">
                <el-form-item class="form_item" prop="username">
                    <label slot="label" class="label_text">用户名</label>
                    <el-input v-model="personInfo.username" class="form_item_input" size="small"></el-input>
                </el-form-item>
                <el-form-item class="form_item" prop="password">
                    <label slot="label" class="label_text">密码</label>
                    <el-input type="password" v-model="personInfo.password" class="form_item_input" size="small"></el-input>
                </el-form-item>
                <el-form-item class="form_item" prop="rePassword">
                    <label slot="label" class="label_text">重复密码</label>
                    <el-input type="password" v-model="personInfo.rePassword" class="form_item_input" size="small"></el-input>
                </el-form-item>
                <el-form-item class="form_item" prop="phone">
                    <label slot="label" class="label_text">手机号</label>
                    <el-input v-model="personInfo.phone" class="form_item_input" size="small"></el-input>
                </el-form-item>
                <el-form-item class="form_item" prop="email">
                    <label slot="label" class="label_text">邮箱</label>
                    <el-input v-model="personInfo.email" class="form_item_input" size="small"></el-input>
                </el-form-item>
                <el-form-item class="form_item">
                    <label slot="label" class="label_text">年龄</label>
                    <el-input v-model="personInfo.age" class="form_item_input" size="small"></el-input>
                </el-form-item>
                <el-form-item class="form_item">
                    <label slot="label" class="label_text">性别</label>
                    <!-- <el-input v-model="personInfo.sex" class="form_item_input" size="small"></el-input> -->
                    <el-radio v-model="personInfo.sex" label="1" border size="small" class="item_radio">男</el-radio>
                    <el-radio v-model="personInfo.sex" label="0" border size="small" class="item_radio">女</el-radio>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="register" size="small">注册</el-button>
                    <el-button size="small" @click="back">返回</el-button>
                </el-form-item>
            </el-form>
        </div>
  </div>
</template>
<script>
export default {
    name: 'register',
    data(){
        var validatePwd = (rule,value,callback) => {
            if(this.personInfo.rePassword === ''){
                callback(new Error('请再次输入密码'))
            }else if(this.personInfo.rePassword !== this.personInfo.password){
                callback(new Error('两次密码输入不一致'))
            }else{
                console.log('进去未知错误')
                callback()
            }
        }
        return{
            personInfo: {
                username:'',
                password:'',
                rePassword:'',
                salt: '',
                email:'',
                phone:'',
                sex: '1',
                age: '',
                status: '1',
            },
            formRule:{
                phone: [{pattern:/^1[3456789]\d{9}$/,message:'请输入正确的电话格式'}],
                email: [{pattern:/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,message:'请输入正确的邮箱格式',trigger: 'blur'}],
                rePassword: [{validator:validatePwd,required:true}],
                password: [{trigger: 'blur',required:true}],
                username: [{trigger: 'blur',required:true}],
            },
            
            
        }
    },
    methods:{
        register(){
            this.$refs['registerForm'].validate(value => {
                if(value){
                    this.$router.push({
                        path: '/'
                    })
                }else{
                    this.$message.error('请完善信息')
                }
            })
        },
        back(){
            this.$router.back(-1)
        }
    }
}
</script>




